.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  pointer-events: auto;
  background-color: var(--backdrop);
  z-index: 999;
}

.modal {
  border: 1px solid var(--modal-border-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  /* height: 234px; */
  border-radius: 8px;
  background-color: var(--modal-back);
  padding: 24px;
}

.close_btn {
  color: var(--main-color);
  position: absolute;
  top: 14px;
  right: 14px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.close_btn:hover {
  color: var(--accent-color-focused);
}

.icon {
  transform: rotate(45deg);
  width: 18px;
  height: 18px;
  stroke: currentColor;
}
